*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Variables for consistent theming */
:root {
  --primary: #6366f1;
  --primary-dark: #4f46e5;
  --secondary: #10b981;
  --danger: #ef4444;
  --warning: #f59e0b;
  --background: #1f2937;
  --surface: #374151;
  --surface-light: #4b5563;
  --text-primary: #f9fafb;
  --text-secondary: #d1d5db;
  --border: #4b5563;
  --shadow: rgba(0, 0, 0, 0.25);
  --transition: all 0.3s ease;
  --radius: 12px;
  --radius-sm: 6px;
}

/* Base styles */
body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background);
  color: var(--text-primary);
  line-height: 1.6;
  min-height: 100vh;
}

#app {
  display: flex;
  flex-direction: column;
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
}

/* Header styles */
header {
  text-align: center;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  border-radius: var(--radius);
  box-shadow: 0 10px 25px var(--shadow);
}

header h1 {
  font-size: 2.5rem;
  font-weight: 800;
  background: linear-gradient(to right, #fff, #e0e7ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0.5rem;
}

/* Main layout */
main {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 24px;
  flex: 1;
}

/* Projects sidebar */
#projects-sidebar {
  background: var(--surface);
  padding: 24px;
  border-radius: var(--radius);
  box-shadow: 0 4px 20px var(--shadow);
  height: fit-content;
  position: sticky;
  top: 20px;
}

#projects-sidebar h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
}

#projects-sidebar h2::before {
  content: "📋";
}

/* Todos section */
#todos-section {
  background: var(--surface);
  padding: 24px;
  border-radius: var(--radius);
  box-shadow: 0 4px 20px var(--shadow);
  min-height: 600px;
}

#current-project-title {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
}

#current-project-title::before {
  content: "📌";
}

/* Button styles */
button {
  background: var(--primary);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: var(--radius-sm);
  cursor: pointer;
  margin-bottom: 10px;
  font-weight: 500;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

button:hover {
  background: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.4);
}

button:active {
  transform: translateY(0);
}

#new-project-btn::before {
  content: "+";
  font-weight: bold;
  font-size: 1.2rem;
}

#new-todo-btn::before {
  content: "+";
  font-weight: bold;
  font-size: 1.2rem;
}

.delete-btn {
  background: var(--danger);
}

.delete-btn:hover {
  background: #dc2626;
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.4);
}

/* Todo items */
.todo-item {
  border: 1px solid var(--border);
  padding: 20px;
  margin-bottom: 16px;
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: var(--transition);
  background: var(--surface-light);
  position: relative;
  overflow: hidden;
}

.todo-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  transition: var(--transition);
}

.todo-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 16px var(--shadow);
}

.todo-item:hover::before {
  width: 6px;
}

.todo-item.high-priority::before {
  background: var(--danger);
}

.todo-item.medium-priority::before {
  background: var(--warning);
}

.todo-item.low-priority::before {
  background: var(--secondary);
}

.todo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.todo-header h3 {
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--text-primary);
}

.todo-actions {
  display: flex;
  gap: 8px;
}

.todo-actions button {
  padding: 8px 12px;
  font-size: 0.9rem;
  margin-bottom: 0;
}

/* Empty state */
#todos-container:empty::after {
  content: "No todos yet. Add one to get started!";
  display: block;
  text-align: center;
  padding: 40px;
  color: var(--text-secondary);
  font-style: italic;
}

/* Projects list */
#projects-list {
  list-style: none;
  margin-top: 16px;
}

#projects-list li {
  margin-bottom: 10px;
}

#projects-list button {
  width: 100%;
  justify-content: flex-start;
  background: transparent;
  color: var(--text-secondary);
  padding: 12px 16px;
  border-radius: var(--radius-sm);
  transition: var(--transition);
}

#projects-list button:hover {
  background: var(--surface-light);
  color: var(--text-primary);
  transform: none;
  box-shadow: none;
}

#projects-list button.active {
  background: var(--primary);
  color: white;
}

/* Modal Styles */
.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.modal-content {
  background-color: var(--surface);
  margin: 10% auto;
  padding: 30px;
  border-radius: var(--radius);
  width: 90%;
  max-width: 500px;
  position: relative;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease;
}

@keyframes slideIn {
  from { transform: translateY(-50px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.close {
  color: var(--text-secondary);
  float: right;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  right: 20px;
  top: 15px;
  cursor: pointer;
  transition: var(--transition);
}

.close:hover,
.close:focus {
  color: var(--text-primary);
  transform: rotate(90deg);
}

.modal-content h2 {
  margin-bottom: 24px;
  font-size: 1.8rem;
  color: var(--text-primary);
  font-weight: 600;
}

/* Form styles */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--text-primary);
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--border);
  border-radius: var(--radius-sm);
  box-sizing: border-box;
  background: var(--surface-light);
  color: var(--text-primary);
  font-family: inherit;
  transition: var(--transition);
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.3);
}

.form-group textarea {
  min-height: 100px;
  resize: vertical;
}

/* Checklist styles */
.checklist-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  gap: 10px;
}

.checklist-item input[type="checkbox"] {
  margin-right: 0;
  width: auto;
  flex-shrink: 0;
}

.checklist-item input[type="text"] {
  flex: 1;
}

.delete-checklist-item {
  background: var(--danger);
  padding: 6px 10px;
  border-radius: var(--radius-sm);
  margin-bottom: 0;
}

/* Todo details */
.detail-row {
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border);
}

.detail-row:last-child {
  border-bottom: none;
}

.detail-label {
  font-weight: 600;
  color: var(--text-primary);
  margin-right: 8px;
  display: inline-block;
  min-width: 100px;
}

/* Responsive design */
@media (max-width: 900px) {
  main {
    grid-template-columns: 1fr;
  }
  
  #projects-sidebar {
    position: static;
    margin-bottom: 20px;
  }
  
  .modal-content {
    margin: 5% auto;
    width: 95%;
  }
}

@media (max-width: 600px) {
  #app {
    padding: 10px;
  }
  
  header h1 {
    font-size: 2rem;
  }
  
  .todo-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  
  .todo-actions {
    width: 100%;
    justify-content: flex-end;
  }
}

/* Utility classes */
.text-center {
  text-align: center;
}

.mt-20 {
  margin-top: 20px;
}

.mb-20 {
  margin-bottom: 20px;
}

/* Animation for new todos */
@keyframes highlight {
  0% { background-color: rgba(16, 185, 129, 0.3); }
  100% { background-color: transparent; }
}

.todo-item.highlight {
  animation: highlight 1.5s ease;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,qBAAqB;AACrB;;;EAGE,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;;AAEA,qCAAqC;AACrC;EACE,kBAAkB;EAClB,uBAAuB;EACvB,oBAAoB;EACpB,iBAAiB;EACjB,kBAAkB;EAClB,qBAAqB;EACrB,kBAAkB;EAClB,wBAAwB;EACxB,uBAAuB;EACvB,yBAAyB;EACzB,iBAAiB;EACjB,6BAA6B;EAC7B,2BAA2B;EAC3B,cAAc;EACd,gBAAgB;AAClB;;AAEA,gBAAgB;AAChB;EACE,uGAAuG;EACvG,SAAS;EACT,UAAU;EACV,mCAAmC;EACnC,0BAA0B;EAC1B,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,iBAAiB;EACjB,cAAc;EACd,aAAa;EACb,iBAAiB;AACnB;;AAEA,kBAAkB;AAClB;EACE,kBAAkB;EAClB,mBAAmB;EACnB,eAAe;EACf,wEAAwE;EACxE,4BAA4B;EAC5B,qCAAqC;AACvC;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,oDAAoD;EACpD,6BAA6B;EAC7B,oCAAoC;EACpC,qBAAqB;AACvB;;AAEA,gBAAgB;AAChB;EACE,aAAa;EACb,gCAAgC;EAChC,SAAS;EACT,OAAO;AACT;;AAEA,qBAAqB;AACrB;EACE,0BAA0B;EAC1B,aAAa;EACb,4BAA4B;EAC5B,oCAAoC;EACpC,mBAAmB;EACnB,gBAAgB;EAChB,SAAS;AACX;;AAEA;EACE,iBAAiB;EACjB,qBAAqB;EACrB,0BAA0B;EAC1B,gBAAgB;EAChB,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,aAAa;AACf;;AAEA,kBAAkB;AAClB;EACE,0BAA0B;EAC1B,aAAa;EACb,4BAA4B;EAC5B,oCAAoC;EACpC,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;EACjB,qBAAqB;EACrB,0BAA0B;EAC1B,gBAAgB;EAChB,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,aAAa;AACf;;AAEA,kBAAkB;AAClB;EACE,0BAA0B;EAC1B,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,+BAA+B;EAC/B,eAAe;EACf,mBAAmB;EACnB,gBAAgB;EAChB,6BAA6B;EAC7B,oBAAoB;EACpB,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,+BAA+B;EAC/B,2BAA2B;EAC3B,6CAA6C;AAC/C;;AAEA;EACE,wBAAwB;AAC1B;;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,mBAAmB;EACnB,6CAA6C;AAC/C;;AAEA,eAAe;AACf;EACE,+BAA+B;EAC/B,aAAa;EACb,mBAAmB;EACnB,+BAA+B;EAC/B,eAAe;EACf,6BAA6B;EAC7B,gCAAgC;EAChC,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,UAAU;EACV,YAAY;EACZ,6BAA6B;AAC/B;;AAEA;EACE,2BAA2B;EAC3B,oCAAoC;AACtC;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,aAAa;EACb,8B