content;\n  position: sticky;\n  top: 20px;\n}\n\n#projects-sidebar h2 {\n  font-size: 1.5rem;\n  margin-bottom: 1.5rem;\n  color: var(--text-primary);\n  font-weight: 600;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n\n#projects-sidebar h2::before {\n  content: \"📋\";\n}\n\n/* Todos section */\n#todos-section {\n  background: var(--surface);\n  padding: 24px;\n  border-radius: var(--radius);\n  box-shadow: 0 4px 20px var(--shadow);\n  min-height: 600px;\n}\n\n#current-project-title {\n  font-size: 1.8rem;\n  margin-bottom: 1.5rem;\n  color: var(--text-primary);\n  font-weight: 600;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n\n#current-project-title::before {\n  content: \"📌\";\n}\n\n/* Button styles */\nbutton {\n  background: var(--primary);\n  color: white;\n  border: none;\n  padding: 12px 20px;\n  border-radius: var(--radius-sm);\n  cursor: pointer;\n  margin-bottom: 10px;\n  font-weight: 500;\n  transition: var(--transition);\n  display: inline-flex;\n  align-items: center;\n  gap: 8px;\n}\n\nbutton:hover {\n  background: var(--primary-dark);\n  transform: translateY(-2px);\n  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.4);\n}\n\nbutton:active {\n  transform: translateY(0);\n}\n\n#new-project-btn::before {\n  content: \"+\";\n  font-weight: bold;\n  font-size: 1.2rem;\n}\n\n#new-todo-btn::before {\n  content: \"+\";\n  font-weight: bold;\n  font-size: 1.2rem;\n}\n\n.delete-btn {\n  background: var(--danger);\n}\n\n.delete-btn:hover {\n  background: #dc2626;\n  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.4);\n}\n\n/* Todo items */\n.todo-item {\n  border: 1px solid var(--border);\n  padding: 20px;\n  margin-bottom: 16px;\n  border-radius: var(--radius-sm);\n  cursor: pointer;\n  transition: var(--transition);\n  background: var(--surface-light);\n  position: relative;\n  overflow: hidden;\n}\n\n.todo-item::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 4px;\n  height: 100%;\n  transition: var(--transition);\n}\n\n.todo-item:hover {\n  transform: translateY(-3px);\n  box-shadow: 0 6px 16px var(--shadow);\n}\n\n.todo-item:hover::before {\n  width: 6px;\n}\n\n.todo-item.high-priority::before {\n  background: var(--danger);\n}\n\n.todo-item.medium-priority::before {\n  background: var(--warning);\n}\n\n.todo-item.low-priority::before {\n  background: var(--secondary);\n}\n\n.todo-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 12px;\n}\n\n.todo-header h3 {\n  font-size: 1.3rem;\n  font-weight: 600;\n  color: var(--text-primary);\n}\n\n.todo-actions {\n  display: flex;\n  gap: 8px;\n}\n\n.todo-actions button {\n  padding: 8px 12px;\n  font-size: 0.9rem;\n  margin-bottom: 0;\n}\n\n/* Empty state */\n#todos-container:empty::after {\n  content: \"No todos yet. Add one to get started!\";\n  display: block;\n  text-align: center;\n  padding: 40px;\n  color: var(--text-secondary);\n  font-style: italic;\n}\n\n/* Projects list */\n#projects-list {\n  list-style: none;\n  margin-top: 16px;\n}\n\n#projects-list li {\n  margin-bottom: 10px;\n}\n\n#projects-list button {\n  width: 100%;\n  justify-content: flex-start;\n  background: transparent;\n  color: var(--text-secondary);\n  padding: 12px 16px;\n  border-radius: var(--radius-sm);\n  transition: var(--transition);\n}\n\n#projects-list button:hover {\n  background: var(--surface-light);\n  color: var(--text-primary);\n  transform: none;\n  box-shadow: none;\n}\n\n#projects-list button.active {\n  background: var(--primary);\n  color: white;\n}\n\n/* Modal Styles */\n.modal {\n  display: none;\n  position: fixed;\n  z-index: 1000;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  overflow: auto;\n  background-color: rgba(0, 0, 0, 0.7);\n  backdrop-filter: blur(5px);\n  animation: fadeIn 0.3s ease;\n}\n\n@keyframes fadeIn {\n  from { opacity: 0; }\n  to { opacity: 1; }\n}\n\n.modal-content {\n  background-color: var(--surface);\n  margin: 10% auto;\n  padding: 30px;\n  border-radius: var(--radius);\n  width: 90%;\n  max-width: 500px;\n  position: relative;\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);\n  animation: slideIn 0.3s ease;\n}\n\n@keyframes slideIn {\n  from { transform: translateY(-50px); opacity: 0; }\n  to { transform: translateY(0); opacity: 1; }\n}\n\n.close {\n  color: var(--text-secondary);\n  float: right;\n  font-size: 28px;\n  font-weight: bold;\n  position: absolute;\n  right: 20px;\n  top: 15px;\n  cursor: pointer;\n  transition: var(--transition);\n}\n\n.close:hover,\n.close:focus {\n  color: var(--text-primary);\n  transform: rotate(90deg);\n}\n\n.modal-content h2 {\n  margin-bottom: 24px;\n  font-size: 1.8rem;\n  color: var(--text-primary);\n  font-weight: 600;\n}\n\n/* Form styles */\n.form-group {\n  margin-bottom: 20px;\n}\n\n.form-group label {\n  display: block;\n  margin-bottom: 8px;\n  font-weight: 500;\n  color: var(--text-primary);\n}\n\n.form-group input,\n.form-group textarea,\n.form-group select {\n  width: 100%;\n  padding: 12px 16px;\n  border: 1px solid var(--border);\n  border-radius: var(--radius-sm);\n  box-sizing: border-box;\n  background: var(--surface-light);\n  color: var(--text-primary);\n  font-family: inherit;\n  transition: var(--transition);\n}\n\n.form-group input:focus,\n.form-group textarea:focus,\n.form-group select:focus {\n  outline: none;\n  border-color: var(--primary);\n  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.3);\n}\n\n.form-group textarea {\n  min-height: 100px;\n  resize: vertical;\n}\n\n/* Checklist styles */\n.checklist-item {\n  display: flex;\n  align-items: center;\n  margin-bottom: 10px;\n  gap: 10px;\n}\n\n.checklist-item input[type=\"checkbox\"] {\n  margin-right: 0;\n  width: auto;\n  flex-shrink: 0;\n}\n\n.checklist-item input[type=\"text\"] {\n  flex: 1;\n}\n\n.delete-checklist-item {\n  background: var(--danger);\n  padding: 6px 10px;\n  border-radius: var(--radius-sm);\n  margin-bottom: 0;\n}\n\n/* Todo details */\n.detail-row {\n  margin-bottom: 16px;\n  padding-bottom: 16px;\n  border-bottom: 1px solid var(--border);\n}\n\n.detail-row:last-child {\n  border-bottom: none;\n}\n\n.detail-label {\n  font-weight: 600;\n  color: var(--text-primary);\n  margin-right: 8px;\n  display: inline-block;\n  min-width: 100px;\n}\n\n/* Responsive design */\n@media (max-width: 900px) {\n  main {\n    grid-template-columns: 1fr;\n  }\n  \n  #projects-sidebar {\n    position: static;\n    margin-bottom: 20px;\n  }\n  \n  .modal-content {\n    margin: 5% auto;\n    width: 95%;\n  }\n}\n\n@media (max-width: 600px) {\n  #app {\n    padding: 10px;\n  }\n  \n  header h1 {\n    font-size: 2rem;\n  }\n  \n  .todo-header {\n    flex-direction: column;\n    align-items: flex-start;\n    gap: 10px;\n  }\n  \n  .todo-actions {\n    width: 100%;\n    justify-content: flex-end;\n  }\n}\n\n/* Utility classes */\n.text-center {\n  text-align: center;\n}\n\n.mt-20 {\n  margin-top: 20px;\n}\n\n.mb-20 {\n  margin-bottom: 20px;\n}\n\n/* Animation for new todos */\n@keyframes highlight {\n  0% { background-color: rgba(16, 185, 129, 0.3); }\n  100% { background-color: transparent; }\n}\n\n.todo-item.highlight {\n  animation: highlight 1.5s ease;\n}"],"sourceRoot":""}]);
